import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-auto-sale',
  templateUrl: './auto-sale.component.html',
  styleUrls: ['./auto-sale.component.scss']
})
export class AutoSaleComponent implements OnInit {

  constructor(
    private router:Router
  ) { }

  ngOnInit(): void {
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee'] )
  }

  gotoPriceList(){
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/auto-sale/price-list']  )
  }
  gotoPriceCompare(){
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/auto-sale/price-compare']  )
  }
 

}
