import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AutoSaleComponent } from './auto-sale.component';

const routes: Routes = [
  {
    path: '',
    component: AutoSaleComponent
  },
  {
    path: 'price-list',
    loadChildren: () => import('./price-list/price-list.module').then(m => m.PriceListModule)
  },  
  {
    path: 'price-compare',
    loadChildren: () => import('./price-comparison/price-comparison.module').then(m => m.PriceComparisonModule)
  },
]


@NgModule({
  declarations: [
    AutoSaleComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class AutoSaleModule { }
